@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Gilroy-Bold";
  src: url("../fonts/Gilroy-Bold.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Gilroy-Bold", sans-serif;
  font-weight: 700;
}

.visibility-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  opacity: 0 !important;
  pointer-events: none !important;
  overflow: hidden !important;
  z-index: -1;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

@font-face {
  font-family: "font-icon";
  src: url("../f-icons/general-icons.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
[class*=font-icon]::before,
[class*=font-icon]::after {
  display: inline-block;
  font-family: "font-icon";
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 30px;
}

.custom-list__item a::before, .related-services__link::before, .social-list a::before {
  font-size: 1.875rem;
  display: inline-block;
  font-family: "font-icon";
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.custom-list__item a::before, .related-services__link::before {
  content: "\e900";
}

.linkedIn::before {
  content: "\e901";
}

.user::before {
  content: "\e902";
}

.tg::before {
  content: "\e903";
}

.x::before {
  content: "\e904";
}

.icon-square {
  display: inline-flex;
  align-items: center;
  padding: 0.5625rem;
  justify-content: center;
  border: 1px solid rgba(255, 255, 255, 0.4);
  border-radius: 5px;
}

html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden],
template {
  display: none;
}

a {
  background-color: transparent;
}

a:active,
a:hover {
  outline: 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

mark {
  background: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
}

pre {
  overflow: auto;
}

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  margin: 0;
}

button {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  line-height: normal;
}

input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  height: auto;
}

input[type=search] {
  box-sizing: content-box;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
}

optgroup {
  font-weight: bold;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

:root {
  --default-indent: 100px;
  --mobile-indent: 60px;
}

* {
  box-sizing: border-box;
}

html,
body {
  font-family: "Gilroy", sans-serif;
  font-size: 16px;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
}

body {
  color: #fff;
  background-color: #0C1525;
  min-width: 320px;
  line-height: 1.4;
}

a {
  transition: all 0.3s ease-out;
}

img {
  display: inherit;
  height: auto;
  max-width: 100%;
}

.container {
  max-width: 1100px;
  padding: 0 20px;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #fff;
  margin-top: 0;
  line-height: 1.2;
  font-weight: normal;
}

h1 {
  font-size: 2.125rem;
  margin-bottom: 0.5em;
}
@media only screen and (min-width: 991px) {
  h1 {
    font-size: 3.125rem;
  }
}

h2 {
  font-size: 1.625rem;
  margin-bottom: 2.5rem;
}
@media only screen and (min-width: 768px) {
  h2 {
    font-size: 2.625rem;
  }
}

h3 {
  font-size: 1.25rem;
  line-height: 1.4;
  margin-bottom: 1.25rem;
}

p {
  font-size: 1rem;
  margin-top: 0;
}

.mb-100 {
  margin-bottom: var(--mobile-indent);
}
@media only screen and (min-width: 768px) {
  .mb-100 {
    margin-bottom: var(--default-indent);
  }
}

.node-page h1 {
  text-align: center;
  margin-bottom: 1.5em;
}
.node-page p {
  margin-bottom: 1.5em;
}
.node-page ul,
.node-page ol {
  padding-left: 2rem;
  margin-bottom: 2rem;
}
.node-page ul li,
.node-page ol li {
  margin-bottom: 0.5rem;
}
.node-page ul li::before,
.node-page ol li::before {
  margin-left: -1rem;
}
.node-page ol {
  counter-reset: list 0;
}
.node-page ol li::before {
  counter-increment: list;
  content: counter(list) ". ";
  color: rgba(255, 255, 255, 0.6);
  font-weight: 600;
}
.node-page ul li::before {
  content: " ";
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
  width: 5px;
  height: 5px;
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 50%;
}
.node-page hr {
  margin: 0 0 1.5rem;
  border: 0;
  background: #212524;
  height: 1px;
}

a {
  color: rgba(255, 255, 255, 0.6);
  text-decoration: none;
}
a:hover {
  color: #fff;
}

.page-section {
  padding: var(--mobile-indent) 0;
}
@media only screen and (min-width: 991px) {
  .page-section {
    padding: var(--default-indent) 0;
  }
}

@media only screen and (min-width: 768px) {
  .section-columns {
    display: flex;
    gap: var(--mobile-indent);
  }
}
@media only screen and (min-width: 991px) {
  .section-columns {
    gap: 80px;
  }
}

.block-image {
  flex-basis: 50%;
  order: -1;
  margin-bottom: 40px;
}
@media only screen and (min-width: 768px) {
  .block-image {
    order: initial;
    margin-bottom: 0;
  }
}
@media only screen and (min-width: 991px) {
  .block-image {
    flex-basis: auto;
  }
}

.section-with-bg {
  background: linear-gradient(to right, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.01));
}

.audit-reports {
  padding: 60px 0;
  gap: var(--mobile-indent);
  margin: 0 -20px;
}
@media only screen and (min-width: 768px) {
  .audit-reports {
    border-radius: 20px;
    display: flex;
    padding: 40px;
  }
}
@media only screen and (min-width: 991px) {
  .audit-reports {
    gap: 80px;
  }
}
.audit-reports .block-image {
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent url("../images/plus-bg-big.svg") -30px 15px;
  width: 100%;
}
@media only screen and (min-width: 768px) {
  .audit-reports .block-image {
    background-color: #0C1525;
    border-radius: 20px;
    padding: 166px 45px;
    max-width: 440px;
  }
}
.audit-reports .block-image img {
  padding: 30px 70px;
}
@media only screen and (min-width: 768px) {
  .audit-reports .block-image img {
    padding: 0;
  }
}
.audit-reports .block-info {
  padding: 0 20px;
}
@media only screen and (min-width: 768px) {
  .audit-reports .block-info {
    flex-basis: 50%;
    padding: 100px 0;
  }
}
@media only screen and (min-width: 991px) {
  .audit-reports .block-info {
    max-width: 440px;
    flex-basis: auto;
  }
}

.report-benefits {
  font-size: 1.125rem;
  line-height: 1.6;
  margin: 1.875rem 0;
  color: #fff;
}
.report-benefits li {
  padding-left: 50px;
  position: relative;
}
.report-benefits li::after, .report-benefits li::before {
  content: "";
  position: absolute;
  top: 13px;
  left: 18px;
  width: 1px;
  height: 16px;
  background: linear-gradient(326.32deg, #273dd2 0%, #6bb9ec 77.5%, #7ac4f4 100%);
  transition: transform 0.3s ease;
}
.report-benefits li::before {
  transform: translateY(-50%) rotate(0);
}
.report-benefits li::after {
  transform: translateY(-50%) rotate(90deg);
}
.report-benefits li:not(:last-child) {
  margin-bottom: 20px;
}

.audit-actions .btn-gradient {
  margin-left: 0;
  margin-right: 10px;
}

.section-verify {
  display: flex;
  flex-direction: column;
}
@media only screen and (min-width: 768px) {
  .section-verify {
    flex-direction: row;
    gap: 80px;
  }
}
.section-verify .success-message {
  font-family: "Gilroy-Bold", sans-serif;
  background-color: #fff;
  border-radius: 20px;
  padding: 1.875rem 2.5rem;
  margin-top: 60px;
  display: none;
}
.section-verify .success-message img {
  margin-right: 20px;
}
.section-verify .success-message p {
  font-size: 1.125rem;
  color: #000;
  margin: 0;
  line-height: 2.5;
}
.section-verify .submit-success .success-message {
  display: flex;
}
.section-verify .submit-success .form {
  display: none;
}

.verify-form {
  max-width: 480px;
}
.verify-form p {
  font-size: 1.125rem;
}
.verify-form .form {
  margin-top: 60px;
}

.member-status {
  display: flex;
  justify-content: center;
  flex: 1 0 auto;
  align-self: center;
  margin-bottom: 40px;
  order: -1;
}
@media only screen and (min-width: 768px) {
  .member-status {
    order: initial;
    margin-bottom: 0;
  }
}
.member-status__image {
  padding: 40px 65px 50px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
}

.site-header {
  display: flex;
  padding-top: 18px;
}
@media only screen and (min-width: 991px) {
  .site-header {
    padding-top: 30px;
  }
}
.site-header .logo {
  max-width: 90px;
  z-index: 100;
}
.site-header .logo svg {
  max-width: 100%;
}
@media only screen and (min-width: 991px) {
  .site-header .logo {
    max-width: none;
  }
}

.main-nav {
  margin-left: auto;
  display: flex;
}
.main-nav__container {
  background: #0C1525;
  display: flex;
  flex-direction: column;
  padding-top: 120px;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
  transform: translateY(-100%);
  transition: transform 0.5s ease;
}
.show-menu .main-nav__container {
  transform: translateY(0);
}
.main-nav__list {
  margin-bottom: var(--mobile-indent);
  opacity: 0;
  transform: translateY(-80%);
  transition: transform 0.5s ease 0.2s, opacity 0.5s ease 0.2s;
}
.show-menu .main-nav__list {
  opacity: 1;
  transform: translateY(0);
}
@media only screen and (min-width: 991px) {
  .main-nav__list {
    margin-bottom: 0;
    opacity: 1;
  }
}
@media only screen and (min-width: 991px) {
  .main-nav__list, .main-nav__container {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: static;
    width: auto;
    height: auto;
    padding: 0;
    transform: none;
  }
}
.main-nav__item {
  border-radius: 10px;
  border: 1px solid transparent;
  margin-bottom: 10px;
}
@media only screen and (min-width: 991px) {
  .main-nav__item {
    margin: 0 5px;
  }
}
.main-nav__item:hover {
  background: linear-gradient(to right, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.01));
}
.main-nav__item.active {
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: linear-gradient(to right, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.01));
}
.main-nav__link {
  padding: 0.5rem 1rem;
  display: block;
  color: #fff;
  line-height: 1.191;
  text-align: center;
}
@media only screen and (min-width: 991px) {
  .main-nav__link {
    text-align: left;
  }
}
.main-nav__link:hover {
  color: #fff;
}
.main-nav__link.active {
  background: linear-gradient(326.32deg, #273dd2 0%, #6bb9ec 77.5%, #7ac4f4 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.main-nav__contact-btn {
  align-self: center;
  z-index: 100;
}

.lang-switcher {
  position: relative;
  opacity: 0;
  transform: translateY(150%);
  transition: transform 0.5s ease 0.3s, opacity 0.5s ease 0.3s;
}
.show-menu .lang-switcher {
  transform: none;
  opacity: 1;
}
@media only screen and (min-width: 991px) {
  .lang-switcher {
    margin-left: 40px;
    transform: none;
    opacity: 1;
  }
}
.lang-switcher:hover .lang-switcher-current {
  background: linear-gradient(to right, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.01));
}
.lang-switcher:hover .lang-switcher-list-container {
  display: block;
}
.lang-switcher img {
  margin-right: 0.5rem;
}
.lang-switcher-current {
  display: flex;
  padding: 6px 10px;
  border-radius: 99rem;
  cursor: pointer;
}
.lang-switcher-list-container {
  position: absolute;
  transform: translateX(-50%);
  top: 100%;
  left: 50%;
  padding-top: 10px;
  display: none;
}
@media only screen and (min-width: 991px) {
  .lang-switcher-list-container {
    right: 0;
    left: auto;
    transform: none;
  }
}
.lang-switcher-list {
  min-width: 120px;
  border-radius: 10px;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.01));
  padding: 6px 10px;
}
.lang-switcher-item {
  display: flex;
  cursor: pointer;
}

.menu-toggle {
  border: 0;
  padding: 0;
  appearance: none;
  background: transparent;
  position: relative;
  width: 40px;
  height: 40px;
  margin-left: 10px;
  z-index: 100;
}
@media only screen and (min-width: 991px) {
  .menu-toggle {
    display: none;
  }
}
.menu-toggle i {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 1px;
  width: 20px;
  background: linear-gradient(326.32deg, #273dd2 0%, #6bb9ec 77.5%, #7ac4f4 100%);
  border-radius: 1px;
  transform: translateX(-50%);
}
.show-menu .menu-toggle i {
  background: transparent;
}
.menu-toggle i::before, .menu-toggle i::after {
  content: "";
  position: absolute;
  right: 0;
  height: 1px;
  border-radius: 1px;
  background: linear-gradient(326.32deg, #273dd2 0%, #6bb9ec 77.5%, #7ac4f4 100%);
  transition: transform 0.3s ease, top 0.3s ease, bottom 0.3s ease;
}
.menu-toggle i::before {
  width: 24px;
  top: -6px;
}
.menu-toggle i::after {
  width: 16px;
  bottom: -6px;
}
.show-menu .menu-toggle i::before {
  top: 0;
  transform: rotate(45deg);
}
.show-menu .menu-toggle i::after {
  bottom: 0;
  transform: rotate(-45deg);
  width: 24px;
}

.site-footer {
  padding-bottom: 20px;
}

.footer {
  color: #fff;
}
.footer__top_menu {
  padding: var(--mobile-indent) 0 40px;
  display: flex;
  flex-direction: column;
  gap: var(--mobile-indent);
}
@media only screen and (min-width: 768px) {
  .footer__top_menu {
    flex-direction: row;
    gap: 0;
    padding: var(--default-indent) 0 80px;
  }
}
.footer__blocks-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 40px;
}
@media only screen and (min-width: 768px) {
  .footer__blocks-list {
    margin-bottom: 80px;
    gap: 20px;
  }
}
@media only screen and (min-width: 991px) {
  .footer__blocks-list {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .footer__blocks-list .item-50 {
    width: calc(50% - 11px);
  }
  .footer__blocks-list .item-33 {
    width: calc(33% - 11px);
  }
}
.footer__blocks-list .block-link {
  display: flex;
  align-items: center;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 20px;
  position: relative;
  background: #0C1525;
  transform: translate(0);
  transform-style: preserve-3d;
}
.footer__blocks-list .block-link:hover::before {
  inset: -4px;
}
.footer__blocks-list .block-link::before {
  content: "";
  position: absolute;
  inset: 0;
  transform: translate3d(0, 0, -1px);
  background: linear-gradient(326.32deg, #273dd2 0%, #6bb9ec 77.5%, #7ac4f4 100%);
  border-radius: inherit;
  transition: inset 0.3s ease;
}
.footer__blocks-list .block-link img {
  margin-right: 20px;
  max-width: var(--mobile-indent);
}
@media only screen and (min-width: 768px) {
  .footer__blocks-list .block-link img {
    max-width: none;
  }
}
.footer__blocks-list .block-link__title {
  font-size: 1.25rem;
  color: #fff;
  font-family: "Gilroy-Bold", sans-serif;
  display: block;
  margin-bottom: 10px;
}
@media only screen and (min-width: 768px) {
  .footer__blocks-list .block-link__title {
    font-size: 1.625rem;
  }
}
.footer-info {
  line-height: 26px;
}
@media only screen and (min-width: 991px) {
  .footer-info {
    display: flex;
    justify-content: space-between;
  }
}
.footer-info__menu-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
}
@media only screen and (min-width: 991px) {
  .footer-info__menu-wrapper {
    flex-direction: row;
    gap: 0;
    margin-bottom: 0;
  }
}
.footer-info__icon-link {
  color: #fff;
  display: flex;
  font-family: "Gilroy-Bold", sans-serif;
  align-items: center;
  line-height: 26px;
}
.footer-info__icon-link .user {
  color: transparent;
  background: linear-gradient(326.32deg, #273dd2 0%, #6bb9ec 77.5%, #7ac4f4 100%);
  background-clip: text;
  margin-right: 10px;
  padding: 0.25rem;
}
.footer-info__icon-link .user::before {
  font-size: 1.125rem;
}
.footer-info__icon-link:hover .user {
  background: linear-gradient(326.32deg, #273dd2 0%, #6bb9ec 77.5%, #7ac4f4 100%);
  color: #0C1525;
  border-color: #0C1525;
}
.footer-info__menu {
  display: flex;
  gap: 20px;
}
@media only screen and (min-width: 991px) {
  .footer-info__menu {
    margin-left: 40px;
  }
}

.footer-social {
  background: linear-gradient(45deg, silver 0%, #fff 100%);
  padding: 2rem 0;
  text-align: right;
}

.social {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (min-width: 768px) {
  .social {
    margin-left: auto;
  }
}
.social-email {
  display: flex;
