.anchor__list {
  min-width: 320px;
  align-self: flex-start;
  position: sticky;
  top: 0;
  text-transform: uppercase;
  overflow: auto;
  background-color: #0C1525;
  -ms-overflow-style: none;
  scrollbar-width: none;
  z-index: 1;
}
@media only screen and (min-width: 768px) {
  .anchor__list {
    overflow: hidden;
  }
}
@media only screen and (min-width: 991px) {
  .anchor__list {
    width: 100%;
    min-width: 480px;
  }
}
.anchor__list-container {
  display: flex;
}
@media only screen and (min-width: 768px) {
  .anchor__list-container {
    align-items: center;
    flex-direction: column;
  }
}
.anchor__list-item {
  margin-bottom: 10px;
}
.anchor__trigger {
  opacity: 0.6;
  padding: 14px 20px 10px 20px;
  border: 1px solid transparent;
  border-radius: 99rem;
  display: inline-flex;
  transition: color 0.3s ease, border-color 0.3s, opacity 0.3s ease;
  min-width: 250px;
  position: relative;
}
.anchor__trigger.active, .anchor__trigger:hover {
  border-color: rgba(255, 255, 255, 0.6);
  opacity: 1;
}
.anchor__trigger.active::before, .anchor__trigger:hover::before {
  background: linear-gradient(326.32deg, #273dd2 0%, #6bb9ec 77.5%, #7ac4f4 100%);
  border-color: #0C1525;
}
.anchor__trigger::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(-30px, -50%);
  border: 1px solid #fff;
  border-radius: 99rem;
  width: 10px;
  height: 10px;
}
.anchor__item {
  margin-bottom: 10px;
  border-radius: 20px;
  padding: 40px;
}
.anchor__item img {
  margin-bottom: 30px;
}
.anchor__item h3 {
  font-size: 1.625rem;
}
.anchor__label {
  text-transform: uppercase;
  font-family: "Gilroy", sans-serif;
  font-weight: normal;
  letter-spacing: 0.04rem;
  opacity: 0.6;
}

